import React from 'react';
import TestTable from './TestTable';
import axios from 'axios';

class TestPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      headers: [
        {
          key: 'date',
          header: 'DATE',
        },
        {
          key: 'type',
          header: 'TYPE',
        },
        {
          key: 'k_symbol',
          header: 'K_SYMBOL',
        },
        {
          key: 'amount',
          header: 'AMOUNT',
        },
        {
          key: 'balance',
          header: 'BALANCE',
        },
      ],
      rows: [],
    };
  }

  componentDidMount() {
    const apiUrl = 'http://localhost:8080/transaction/getByClient?key=1';
    //const apiUrl = '/transaction/getByClient?key=1';

    axios
      .get(apiUrl)
      .then(repos => {
        var mydata = repos.data;
        var x = 0;
        console.log('This is your data from Hazelcast IMDG', mydata);
        mydata.forEach(function(row) {
          row.id = x.toString();
          x = x + 1;
        });
        console.log('This is your data with id', mydata);
        this.setState({ rows: mydata });
      })
      .catch(function(error) {
        if (error.response) {
          // Request made and server responded
          console.log(error.response.data);
          console.log(error.response.status);
          console.log(error.response.headers);
        } else if (error.request) {
          // The request was made but no response was received
          console.log(error.request);
        } else {
          // Something happened in setting up the request that triggered an Error
          console.log('Error', error.message);
        }
      });
  }

  render() {
    return (
      <div className="bx--grid bx--grid--full-width bx--grid--no-gutter repo-page">
        <div className="bx--row repo-page__r1">
          <div className="bx--col-lg-16">
            <TestTable headers={this.state.headers} rows={this.state.rows} />
          </div>
        </div>
      </div>
    );
  }
}

export default TestPage;
