import React from 'react';
import {
    DataTable,
    TableContainer,
    Table,
    TableHead,
    TableHeader,
    TableBody,
    TableRow,
    TableCell,
  } from "carbon-components-react";
  
  const AccountTable = ({ rows, headers }) => {
    return (
      <DataTable isSortable
        rows={rows}
        headers={headers}
        render={({
          rows,
          headers,
          getHeaderProps
        }) => (
          <TableContainer title="Recent Transactions">
            <Table>
              <TableHead>
                <TableRow>
                  {headers.map(header => (
                    <TableHeader {...getHeaderProps({ header })}>
                      {header.header}
                    </TableHeader>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map(row => (
                  
                    <TableRow key={row.id}>
                      {row.cells.map(cell => (
                        <TableCell key={cell.id}>{cell.value}</TableCell>
                      ))}
                    </TableRow>
                  
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        )}
      />
    );
  };
  
  export default AccountTable;